    justify-content: center;
    font-size: 28px;
}

.layout-head{
    height: 150vh;
    
}
.head{
   background-image: radial-gradient(circle, #5c0067 0%, #00d4ff 100%);
   
}

.form-head{
  
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center; 
  
}

.form-card{
    width: 400px;
    border:  2px solid rgb(84, 49, 49);
    padding: 50px;
    background-color: #ffff;
    position: absolute;
   
 
}

